
import { defineComponent, provide, ref } from 'vue'
export default defineComponent({
    components: {},
    emits: ['confirm', 'cancel'],
    props: {
        title: {
            // 弹窗标题
            type: String,
            default: '',
        },
        content: {
            // 弹窗内容
            type: String,
            default: '确认要删除？',
        },
        confirmButtonText: {
            // 确认按钮内容
            type: [String, Boolean],
            default: '确认',
        },
        cancelButtonText: {
            // 取消按钮内容
            type: [String, Boolean],
            default: '取消',
        },
        width: {
            // 弹窗的宽度
            type: String,
            default: '400px',
        },
        disabled: {
            // 是否禁用
            type: Boolean,
            default: false,
        },
        async: {
            // 是否开启异步关闭
            type: Boolean,
            default: false,
        },
        clickModalClose: {
            // 点击遮罩层关闭对话窗口
            type: Boolean,
            default: true,
        },
        customClass: {
            type: String,
            default: '',
        },
    },
    setup(props, { emit }) {
        const visible = ref(false)

        const handleEvent = (type: 'confirm' | 'cancel') => {
            emit(type)
            if (!props.async || type === 'cancel') close()
        }

        const close = () => {
            visible.value = false
        }
        const open = () => {
            if (props.disabled) return
            visible.value = true
        }
        provide('visible', visible)
        return {
            visible,
            handleEvent,
            close,
            open,
        }
    },
})
